@import "_recipes";
@import "_variables";

footer{
	background-color: $corporativoSecundario;
	padding: 20px 10px;
	p{
		margin: 0;
		color: $white;
	}
	.all{
		display:table;
		width:100%;
		max-width: $maxWidth;
		margin: 0 auto;
		> *{
			display:table-cell;
			&.social-networks{
				text-align:right;
				a{
					text-decoration:none;
					font-size:0;
					&:before{
						@include icomoon(20px);
						color: $white;
						margin: 0 10px;
					}
					&.fb{
						&:before{
						  content: "\ea90";
						}
					}
					&.tw{
						&:before{
							content: "\ea96";
						}
					}
					&.ig{
						&:before{
							content: "\ea92";
						}
					}
					&.lk{
						&:before{
							content: "\eaca";
						}
					}
					&:hover{
						&:before{
							color: $corporativoPrincipal;
						}
					}
				}
			}
		}
	}
}