@import "_recipes";
@import "_variables";

header {
  background-color: $corporativoPrincipal;
  padding: 10px;
  position: fixed;
  width:100%;
}

.all {
  display: table;
  width: 100%;
  max-width: $maxWidth;
  margin: 0 auto;
  &>* {
    display: table-cell;
    vertical-align: middle;
  }
	.logo {
    font-family: $bold;
    color: $white;
    font-size: 26px;
    width: 11%;
    border: 2px solid white;
    padding: 0 20px;
    &:before {
      content: " ";
      background-image: url('../images/rss2.png');
      height: 22px;
      background-size: 22px;
      background-repeat: no-repeat;
      display: inline-block;
      width: 22px;
      margin-right: 10px;
    }
	}
	.menu{
		width: 89%;
    text-align: right;
    li{
			list-style: none;
			display:inline-block;
			vertical-align:middle;
			padding: 0 12px;
			a{
				font-family: $regular;
				color: $white;
				text-decoration: none;
				&:hover{
					text-decoration:underline;
				}
			}
    }
	}
}
