@import "_recipes";
@import "_variables";

.cabecera {
    max-width: 100%;
    width: 1920px;
    height: 400px;
    object-fit: cover;
}

div.enlace {
    text-align: center;
    padding: 20px 0;
}

.link {
    background-color: $corporativoPrincipal;
    color: $white;
    border: 2px solid $corporativoPrincipal;
    border-radius: 5px;
    font-family: $bold;
    text-decoration: none;
    padding: 5px 20px;
    font-size: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;
    transition: all 0.5s ease-in-out;
    &:hover {
        background-color: $white;
        color: $corporativoPrincipal;
    }
}

form {
	max-width: 700px;
  font-size: 0;
  >div {
    display: inline-block;
    vertical-align: top;
    width: 50%;
    font-size: 16px;
    font-family: $regular;
    padding: 10px 0;
    &.all{
        width:100%;
    }
    label {
        padding-right: 10px;
        font-family: $bold;
    }
    input {
        padding: 5px;
    }
  }
  input[type="button"] {
    display: block;
    cursor:pointer;
    text-align: center;
    margin-bottom: 40px;
    margin-top: 40px;
    background-color: $corporativoPrincipal;
    color: $white;
    border: 2px solid $corporativoPrincipal;
    border-radius: 5px;
    font-family: $bold;
    text-decoration: none;
    padding: 5px 20px;
    font-size: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;
    transition: all 0.5s ease-in-out;
    &:hover {
        background-color: $white;
        color: $corporativoPrincipal;
    }
  }
}

table{
  width: 100%;
  text-align: center;
  thead{
    th{
			font-size: 16px;
			font-family: $bold;
			padding: 5px 10px;
			color: $white;
			background-color: $corporativoPrincipal;
    }
  }
  tbody{
    tr{
      td{
        font-family: $regular;
        padding: 10px 5px;
        &.monumentos.ico, &.alojamientos.ico{
          font-size: 0;
          &:before{
            color: $corporativoPrincipal;
            @include icomoon(22px);
          }
        }
        &.monumentos.no-ico, &.alojamientos.no-ico{
          font-size: 0;
          &:before{
            color: $corporativoPrincipal;
            @include icomoon(22px);
            content: "\e902";
            color: red;
          }
        }
        &.monumentos.ico{
          &:before{
            content: "\e900";
          }
        }
        &.alojamientos.ico{
          &:before{
            content: "\e901";
          }
        }
      }
    }
  }
}
